.animation-appear {
    animation: appearBottom 0.5s ease-in-out;
    overflow: hidden;
}

.animation-disappear {
    animation: disappearBottom 0.5s ease-in-out;
    overflow: hidden;
    height: 0px;
}

@keyframes appearBottom {
    from {
        height: 0px;
    }
    to {
        height: 50vw;
    }
}
@keyframes disappearBottom {
    from {
        height: 50vw;
    }
    to {
        height: 0px;
    }
}

